.slider-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 85vh;
    margin-top: 2rem;
  }
  
  .slider-wrapper {
    display: flex;
    transition: transform 0.5s ease-in-out;
  }
  
  .slide {
    min-width: 100%;
    box-sizing: border-box;
    text-align: center;
    line-height: 85vh; /* Adjust line-height to center content vertically */
    background-color: lightgray; /* Adjust background as needed */
    background-size: contain; /* Ensure the background image covers the slide */
    background-position: center;
  height: 100%;
  }
  .description {
    font-size: 3rem;
    text-align: left; 
    color: yellow;/* Adjust description font size */
  }