#Landing {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #f2f2f2;
}

#machines {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-left: 3%;
    margin-right: 3%;
    margin-top: 5%;
    margin-bottom: 5%;
    text-align: center;
}

.machinebox {
    width: 40%;
    height: 15rem;
    border: 3px solid black;
    border-radius: 10px;
    margin-top: 5%;
    margin-bottom: 5%;
    text-align: center;
    position: relative;
    overflow: hidden;
    transition: transform 0.3s ease; /* Smooth transition */
    
    background-size: cover;
    background-position: center;
}

.machinebox:hover {
    z-index: 9999; /* Move in the z-direction on hover */
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.3);
    transition: all ease 0.3s;
}

.machineimgdiv {
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.machineimg {
    max-width: 100%;
    max-height: 100%;
}

.machinecontdiv {
    width: 100%;
    height: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.8);
}
