/* Description.css */

body {
    font-family: 'Roboto', sans-serif; /* Use a clean and modern font */
    background-color: #f7f7f7; /* Light background color */
    color: #333; /* Dark text color for better readability */
    margin: 0;
    padding: 2px;
}

h1 {
    text-align: center;
    font-size: 36px;
    color: #4f502c;
    margin-bottom: 40px;
    font-family: 'Poppins', sans-serif;
}

.description-container {
    max-width: 100%;
    margin: 0 auto;
    background: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
}

p {
    font-size: 18px;
    line-height: 1.6;
    margin: 10px 0;
}

strong {
    font-size: 20px;
    color: #34495e;
    font-family: 'Poppins', sans-serif;
}

/* Styling for machine name and description */
p strong:first-child {
    font-size: 24px;
    font-weight: 700;
    color: #2980b9;
    margin-right: 10px;
    display: inline-block;
    width: 300px;
    text-align: left;
}


video {
    display: block;
    margin: 20px auto;
    border-radius: 8px;
}

/* Battery and Memory sections styling */
.battery-info, .memory-info {
    display: flex;
    align-items: center;
    margin-top: 10px;
    padding: 10px 0;
    border-top: 1px solid #ddd;
}

.battery, .memory {
    flex: 1;
}
.components-info{
    display: flex;
    flex-direction: column;
}
.bulb {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-left: 90rem;


}
/* Pop-up window container */
.popup-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure the pop-up is on top of other elements */
}

/* Pop-up window content */
.popup-content {
    background-color: white;
    padding: 60px;
    border-radius: 10px;
    width: 50%; /* Adjust as needed */
    max-width: 500px; /* Max width */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 1001;
}

/* Close button for the pop-up */
.popup-close-button {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 18px;
    background: none;
    border: none;
    cursor: pointer;
}

/* Pop-up content example styling */
.popup-content p {
    margin-bottom: 10px;
    font-family: Arial, sans-serif;
    font-size: 16px;
}

/* Button inside the pop-up */
.popup-content button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.suggestions-button{
    background-color: rgb(83, 83, 188);
    color: white;
    border-radius: 5px;
    height: 2.5rem;
    font-size: 1.2rem;
    width: 7rem;
}
.suggestions-button:hover{
    cursor: pointer;
    background-color: #45a049;
    transition: all ease 0.5s;
}
/* Description.css */
.chart-slider {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.chart-container {
    width: 48%;
}

.chart-container h3 {
    text-align: center;
}


.component h3{
    font-size: 2rem;
}
/* Button hover effect */
.popup-content button:hover {
    background-color: #45a049;
}

.blinking-bulb {
    animation: blinking 4s infinite;
}

@keyframes blinking {
    0% { opacity: 1; }
    50% { opacity: 0; }
    100% { opacity: 1; }
}
