/* header.css */

#headerdiv {
    background-color: #f2f2f2; /* Light grey background */
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

#headerdiv::after {
    content: '';
    position: absolute;
    bottom: -20%;
    left: 0;
    width: 100%;
    height: 2rem;
    background: linear-gradient(to bottom, #eeecda, #f7d54d,#e6b800); /* Gradient from yellow to darker yellow */
}

#headerdiv img {
    width: 150px; /* Adjust the size of the logo */
    height: auto;
}

#headerdiv .logout-button,
#headerdiv .auth-button {
    background-color: #ffcc00; /* Yellow background */
    color: #000; /* Black text */
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s;
    margin-left: 1rem;
    font-size: 1.2rem;
    font-weight: 700;
}

#headerdiv .logout-button:hover,
#headerdiv .auth-button:hover {
    background-color: #e6b800; /* Darker yellow on hover */
}
